<template>
  <div>
    
    <div v-if="currentPage==0">
      <h2 class='title'>此目录下为正则表达式相关笔记</h2>
      <div class='title-index' v-for="item in articles" @click="currentPage = item.index" :key="item.index">
        {{item.name}}
      </div>
    </div>
    <div class='markdowns' v-else>
      <AboutRegExp1 v-if="currentPage == 1"/>
      <AboutRegExp2 v-if="currentPage == 2"/>
      <AboutRegExp3 v-if="currentPage == 3"/>
      <AboutRegExp4 v-if="currentPage == 4"/>
      <AboutRegExp5 v-if="currentPage == 5"/>
      <AboutRegExp6 v-if="currentPage == 6"/>
    </div>
  </div>
</template>

<script>
import AboutRegExp1 from 'markdown/AboutRegExp/1-正则简单介绍.md';
import AboutRegExp2 from 'markdown/AboutRegExp/2-常用的正则表达式.md';
import AboutRegExp3 from 'markdown/AboutRegExp/3-Replace方法与正则.md';
import AboutRegExp4 from 'markdown/AboutRegExp/4-正则的捕获.md';
import AboutRegExp5 from 'markdown/AboutRegExp/5-分组捕获和分组引用.md';
import AboutRegExp6 from 'markdown/AboutRegExp/6-正则捕获的贪婪性.md';

export default {
