        {index:2,name:"2-常用的正则表达式"},
        {index:3,name:"3-Replace方法与正则"},
        {index:4,name:"4-正则的捕获"},
        {index:5,name:"5-分组捕获和分组引用"},
        {index:6,name:"6-正则捕获的贪婪性"},
      ]
    }
  },
  components:{
    AboutRegExp1,
    AboutRegExp2,
    AboutRegExp3,
    AboutRegExp4,
    AboutRegExp5,
    AboutRegExp6
  },
  mounted(){
    let index=this.$route.params.index;
    if(index) this.currentPage=index;
  }
}
</script>

